.app {
  display: block;
  min-height: 100vh;
  width: 100%;
  height: 100%;
  transition: all 0.5s ease-in-out;
  &.shadow {
    background-color: rgba(0, 0, 0, 0.5); } }


.d-none {
  display: none; }

.relative {
  position: relative; }


.h-100 {
  height: 100%; }

.mb-05 {
  margin-bottom: 0.5rem; }
.mb-1 {
  margin-bottom: 1rem; }


.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  border: 1px solid transparent;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .25rem;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  cursor: pointer;
  &__block {
    width: 100%; }
  &__primary {
    color: #fff;
    //background-color: #0d6efd
    background-color: $primary;
    border-color: $primary;
    &:hover {
      //background-color: #0b5ed7
      background-color: darken(desaturate($primary, 8.10), 7.84);
      //border-color: #0a58ca
      border-color: darken(desaturate($primary, 8.10), 7.84); } }
  &__success {
    color: $white;
    background-color: $success;
    border-color: $success;
    &:hover {
      background-color: #218838;
      border-color: #1e7e34; } }
  &__danger {
    color: $white;
    background-color: $danger;
    border-color: #b21f2d;
    &:hover {
      background-color: #bd2130;
      border-color: #b21f2d; } } }

.input {
  display: block;
  width: 100%;
  padding: .375rem .75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.2;
  color: $dark;
  background-color: $white;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: .25rem;
  transition: border-color .2s;
  &:focus {
    color: #212529;
    background-color: #fff;
    border-color: #86b7fe;
    box-shadow: 0 0 0 0.05rem rgba(13,110,253,.25); } }


.h1 {
  &__sub {
    font-weight: 300;
    font-size: 2rem; } }

.subtext {
  font-size: 0.9rem;
  color: $secondary;
  & a {
    color: $link; } }

.bg-light {
  background-color: $light; }
