.container {
  max-width: 990px;
  width: 90%;
  margin: 0 auto; }
.content {
  width: 50rem;

  margin: 0 auto; }
.row {
  display: flex; }

.col {
  flex: 1 0 0;
  &__6 {
    flex: 0 0 auto;
    width: 50%; } }

.justify-content-between {
  justify-content: space-between; }
.justify-content-center {
  justify-content: center; }

.align-items-center {
  align-items: center; }

.column {
  flex-direction: column; }
